const initialState = {
  browserName: "",
  imageExtension: ""
};

const setBrowserName = (state = initialState, action) => {
  switch (action.type) {
    case "SET_BROWSER_NAME":
      let imageExtension;
      if (action.payload === "safari/webkit") {
        imageExtension = "webp";
      } else imageExtension = "jpg";
      return { ...state, browserName: action.payload, imageExtension: imageExtension };
    default:
      return state;
  }
};

export default setBrowserName;
